import React from 'react';


class Lobby extends React.Component {
    constructor() {
        super();
    }

    render() {
        return (
            <table>
                <tr>
                    <th>Roomname</th>
                    <th>Number of Players</th>
                </tr>
                <tr>
                    <td>DJ's Room</td>
                    <td>1/2</td>
                </tr>
                <tr>
                    <td>Savy's Room</td>
                    <td>2/2</td>
                </tr>
            </table>
        );
    }
}
export default Lobby



